import React from "react";

function MyArt(){
    return(
        <div className="myArtDiv">
            <h1>My Art</h1>
            <div className="artDiv">
                {/* <img src="images/art1.png" alt="" /> */}
                <div className="mainCarousel">
                    <div id="carouselExampleDark" className="mainCar carousel carousel-dark slide" data-bs-ride="carousel">
                        <div className="carousel-indicators">
                            <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                            <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="1" aria-label="Slide 2"></button>
                            <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="2" aria-label="Slide 3"></button>
                            <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="4" aria-label="Slide 4"></button>
                        </div>
                        <div className="carousel-inner">
                            <div className="carousel-item active" data-bs-interval="3000">
                                <img src="images/art1.png" className="d-block w-100" alt="..."/>
                            </div>
                            <div className="carousel-item" data-bs-interval="3000">
                                <img src="images/art2.png" className="d-block w-100" alt="..." />
                            </div>
                            <div className="carousel-item" data-bs-interval="3000">
                                <img src="images/art3.png" className="d-block w-100" alt="..." />
                            </div>
                            <div className="carousel-item" data-bs-interval="3000">
                                <img src="images/art4.png" className="d-block w-100" alt="..." />
                            </div>
                        </div>
                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleDark" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden"></span>
                        </button>
                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleDark" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden"></span>
                        </button>
                    </div>
                </div>
                
            </div>
        </div>
    );
}

export default MyArt;